body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.color-primary-0 { color: #58D0F2 }	/* Main Primary color */
.color-primary-1 { color: #B7ECFC }
.color-primary-2 { color: #82DDF7 }
.color-primary-3 { color: #32C1EA }
.color-primary-4 { color: #0CACDA }

.color-secondary-1-0 { color: #6783F4 }	/* Main Secondary color (1) */
.color-secondary-1-1 { color: #BECAFC }
.color-secondary-1-2 { color: #8DA2F8 }
.color-secondary-1-3 { color: #4465ED }
.color-secondary-1-4 { color: #1F45DF }

.color-secondary-2-0 { color: #FFCD57 }	/* Main Secondary color (2) */
.color-secondary-2-1 { color: #FFEAB7 }
.color-secondary-2-2 { color: #FFDA82 }
.color-secondary-2-3 { color: #FFC130 }
.color-secondary-2-4 { color: #FFB406 }

.color-complement-0 { color: #FFAC57 }	/* Main Complement color */
.color-complement-1 { color: #FFDBB7 }
.color-complement-2 { color: #FFC182 }
.color-complement-3 { color: #FF9830 }
.color-complement-4 { color: #FF8306 }

.customNavbar{
    background-image: -webkit-linear-gradient(left, #6610f2,#9266b9 ,#bc98db, rgb(187, 234, 240), #58D0F2, #007BC7);
    background-image: linear-gradient(to right, #6610f2,#9266b9 ,#bc98db, rgb(187, 234, 240), #58D0F2, #007BC7);  
}

.navbar-light .navbar-nav .nav-link{
    color: white;
    
}

.random{
    color: white;
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
    vertical-align: -10%;
}
.bg-resources{
    background-color:whitesmoke;
    background-clip: border-box;
}

.add-button{
    background-image:-webkit-linear-gradient(left, #007BC7, rgb(15, 146, 228));
    background-image:linear-gradient(to right, #007BC7, rgb(15, 146, 228));
    border: none;
    box-shadow: 3px 3px 2px grey;
    margin-right: 15px;
    margin-top: 5px;
}

.add-button:hover{
    background-image:-webkit-linear-gradient(left, #012e4b, rgb(15, 146, 228));
    background-image:linear-gradient(to right, #012e4b, rgb(15, 146, 228));
}   

.start-search-button{
    background-image:-webkit-linear-gradient(left, #6610f2, #9266b9 );
    background-image:linear-gradient(to right, #6610f2, #9266b9 );
    box-shadow: 3px 3px 2px grey;
    border:  none;
    margin-right: 15px;
    margin-top: 5px;
}

.start-search-button:hover{
    background-image:-webkit-linear-gradient(left, #1b0341, #9266b9 );
    background-image:linear-gradient(to right, #1b0341, #9266b9 );
}

.custom-scroll{
    overflow-y: scroll;
}

.custom-heading{
    color: #1F45DF;
}

.card-body{
    background-color: rgba(211, 211, 211,0.5);
}
.custom-card-header{
    
    background-image: -webkit-linear-gradient(left,  rgb(228, 233, 252), #bc98db);
    
    background-image: linear-gradient(to right,  rgb(228, 233, 252), #bc98db);
}



.selected{
    background-color: #FFCD57 ;
    color: black;
    border:none;
}

.tag-text-size{
    font-size: 120%;
}

.tag-label{
    background-color: silver;
    border: none;
    color: white;
}

.submit-button{
    background-image:-webkit-linear-gradient(left, #007BC7, rgb(15, 146, 228));
    background-image:linear-gradient(to right, #007BC7, rgb(15, 146, 228));
    border: none;
    box-shadow: 3px 3px 2px grey;
}

.submit-button:hover{
    background-image:-webkit-linear-gradient(left, #012e4b, rgb(15, 146, 228));
    background-image:linear-gradient(to right, #012e4b, rgb(15, 146, 228));
    border:none;
}

.input-group-text {
    background-image: -webkit-linear-gradient(left, rgba(188, 152, 219, 0.5), #bc98db  );
    background-image: linear-gradient(to right, rgba(188, 152, 219, 0.5), #bc98db  );
    width: 130px;
    font-weight: bold; 
  
}

.form-control {

    background-image: -webkit-linear-gradient(left, #fff, rgba(188, 152, 219, 0.2)  );

    background-image: linear-gradient(to right, #fff, rgba(188, 152, 219, 0.2)  );
    
}

.search-button{
    background-image:-webkit-linear-gradient(left, #007BC7, rgb(15, 146, 228));
    background-image:linear-gradient(to right, #007BC7, rgb(15, 146, 228));
    border: none;
    box-shadow: 3px 3px 2px grey;
}

.search-button:hover{
    background-image:-webkit-linear-gradient(left, #012e4b, rgb(15, 146, 228));
    background-image:linear-gradient(to right, #012e4b, rgb(15, 146, 228));
    border:none;    
}

.results-background{
    background-color:whitesmoke;
}

