    background-color: #FFCD57 ;
    color: black;
    border:none;
}

.tag-text-size{
    font-size: 120%;
}

.tag-label{
    background-color: silver;
    border: none;
    color: white;
}

.scroll{
    overflow-y: scroll;
}
